const uniapi = {
  // 跳转到应用内的某个页面, 保留当前页面
  goGavigate(url) {
    uni.navigateTo({
      url,
      fail: (err) => {
        console.log(err)
      }
    })
  },
  // 跳转到 tabBar 页面, 并关闭其他所有非 tabBar 页面
  goSwitchTab(url) {
    uni.switchTab({
      url,
      fail: (err) => {
        console.log(err)
      }
    })
  },
  // showToast 显示消息提示框
  showMessageBox (title) {
    uni.showToast({
      title,
      mask: true,
      icon: "error",
      fail: (err) => {
        console.log(err)
      }
    })
  }
  
}

export default uniapi
