import JSEncrypt from '../../jsencrypt/index.js';

// 密码加密
const generateKey = (params) => {
  let { authorization, password } = params
  let encrypt =new JSEncrypt()
  // 公钥
  let pubKey = `-----BEGIN PUBLIC KEY-----
  MFwwDQYJKoZIhvcNAQEBBQADSwAwSAJBALEomrkYHBtwDpl++GPN7O7fcZxbKxOJ
  Erae49FVfXc4Kavz3bfllExeNbZBbVYYf1Znuq35GNOkwop9id53jn8CAwEAAQ==
  -----END PUBLIC KEY-----`
  
  if (authorization) {
          pubKey = `-----BEGIN PUBLIC KEY-----
  ${authorization}
  -----END PUBLIC KEY-----`
        }
  encrypt.setPublicKey(pubKey)
  return encrypt.encryptLong(password)
}

// 获取当前时间
const timer = () => {
  let date = new Date()
  let year = date.getFullYear()
  let month = date.getMonth() + 1
  let day = date.getDate()
  if (month < 10) {
    month = "0" + month
  }
  if (day < 10) {
    day = "0" + day
  }
  return `${year}-${month}-${day}`
}

// 打开手机 wifi 列表界面
const openAppWifi = () => {
  if (plus.os.name === "Android") { // Android 平台
    let main = plus.android.runtimeMainActivity()
    let Intent = plus.android.importClass("android.content.Intent")
    let mIntent = new Intent('android.settings.WIFI_SETTINGS')
    main.startActivity(mIntent)
  } else { // ios 平台
    let UIApplication = plus.ios.import("UIApplication")
    let NSURL = plus.ios.import("NSURL")
    let setting = NSURL.URLWithSting("app-setting:")
    let application = UIApplication.shareApplication()
    application.openURL(setting)
    plus.ios.deleteObject(setting)
    plus.ios.deleteObject(application)
  }
}

// 获取 android 已连接的 wifi 信息
const getAppWifiInfo = () => {
  let MainActivity = plus.android.runtimeMainActivity()
  let context = plus.android.importClass('android.content.Context')
  plus.android.importClass("android.net.wifi.WifiManager")
  plus.android.importClass("android.net.wifi.WifiInfo")
  plus.android.importClass("android.net.wifi.ScanResult")
  plus.android.importClass("java.util.ArrayList")
  // 获取 wifi 管理实例
  let wifiManager = MainActivity.getSystemService(context.WIFI_SERVICE)
  // 获取当前连接 wifi 的信息
  let info = wifiManager.getConnectionInfo()
  // 获取当前 wifi 连接的 SSID (wifi 名称)
  let ssid = info.getSSID()
  // 这里的获取到的名称是带双引号的, 如 "rtwap"
  ssid = ssid.replace(/(^\"*)|(\"*$)/g, "")
  console.log(5353, info, ssid)
  wifiManager.setWifiEnabled(true) // 打开 wifi, false为关闭
  wifiManager.startScan() // 扫描
  let resultList = wifiManager.getScanResults()
  console.log(132, resultList)
  let len = resultList.size()
  
  for(let i = 0; i < len; i++) {
    console.log(321, resultList.get(i).plusGetAttribute("SSID"))
  }
  return {
    info,
    ssid
  }
  
}

// 密码验证(8-16位数字、字母、特殊字符组合)
const passwordVarify = (pwd) => {
  return new RegExp("^(?=.*[0-9])(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9]).{8,16}$").test(pwd)
}


module.exports = {
  generateKey,
  timer,
  getAppWifiInfo,
  openAppWifi,
  passwordVarify
}